interface Soutenance {
  idSoutenance: number;
  date: string;
  time: string;
  location: string;
  groupNames: string[];
  juryNames: string[];
  status: string;
}

interface StatusConfig {
  color: string;
  bg: string;
  hoverBg: string;
  icon: React.ReactNode;
  text: string;
}

interface Salle {
  idSalle: string;
  nomSalle: string;
}

interface Group {
  idGroupe: string;
  nomGroupe: string;
  nbEtudiants: number;
  students: string[];
}

function getStatusConfig(status: string): StatusConfig {
  switch (status) {
    case "Scheduled":
      return {
        color: "text-blue-700 dark:text-blue-300",
        bg: "bg-blue-100 dark:bg-blue-900/50",
        hoverBg: "hover:bg-blue-200 dark:hover:bg-blue-900/70",
        icon: <CalendarClock className="w-3.5 h-3.5" />,
        text: "Planifi",
      };
    case "Completed":
      return {
        color: "text-green-700 dark:text-green-300",
        bg: "bg-green-100 dark:bg-green-900/50",
        hoverBg: "hover:bg-green-200 dark:hover:bg-green-900/70",
        icon: <CheckCircle2 className="w-3.5 h-3.5" />,
        text: "Termin",
      };
    case "Pending":
      return {
        color: "text-yellow-700 dark:text-yellow-300",
        bg: "bg-yellow-100 dark:bg-yellow-900/50",
        hoverBg: "hover:bg-yellow-200 dark:hover:bg-yellow-900/70",
        icon: <Clock className="w-3.5 h-3.5" />,
        text: "En attente",
      };
    case "Cancelled":
      return {
        color: "text-red-700 dark:text-red-300",
        bg: "bg-red-100 dark:bg-red-900/50",
        hoverBg: "hover:bg-red-200 dark:hover:bg-red-900/70",
        icon: <XCircle className="w-3.5 h-3.5" />,
        text: "Annul",
      };
    default:
      return {
        color: "text-gray-700 dark:text-gray-300",
        bg: "bg-gray-100 dark:bg-gray-800",
        hoverBg: "hover:bg-gray-200 dark:hover:bg-gray-800/70",
        icon: <AlertCircle className="w-3.5 h-3.5" />,
        text: "Inconnu",
      };
  }
}

function StatusBadge({ status }: { status: string }) {
  const config = getStatusConfig(status);

  return (
    <Badge
      variant="outline"
      className={`
        ${config.bg} 
        ${config.color} 
        ${config.hoverBg}
        border-0 
        font-medium 
        gap-1.5
        px-2.5 
        py-0.5 
        transition-colors
        duration-200
        inline-flex
        items-center
        cursor-default
        select-none
        shadow-sm
      `}
    >
      {config.icon}
      {config.text}
    </Badge>
  );
}

function DeleteDialog({
  onDelete,
  idSoutenance,
}: {
  onDelete: () => void;
  idSoutenance: number;
}) {
  return (
    <AlertDialog>
      <AlertDialogTrigger asChild>
        <Button
          variant="ghost"
          size="icon"
          className="hover:bg-red-100 hover:text-red-600 dark:hover:bg-red-900/30"
        >
          <Trash2 className="h-4 w-4" />
          <span className="sr-only">Supprimer la soutenance</span>
        </Button>
      </AlertDialogTrigger>
      <AlertDialogContent className="max-w-md">
        <AlertDialogHeader>
          <AlertDialogTitle>Confirmer la suppression</AlertDialogTitle>
          <AlertDialogDescription>
            tes-vous sr de vouloir supprimer cette soutenance ? Cette action
            est irrversible.
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>Annuler</AlertDialogCancel>
          <AlertDialogAction
            onClick={onDelete}
            className="bg-red-600 hover:bg-red-700 dark:hover:bg-red-700"
          >
            Supprimer
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
  );
}

const handleExportPlanningExcel = async (
  options: {
    groupByJury: boolean;
    includeHeaders: boolean;
    includeTitle: boolean;
    exportFormat?: string;
    colorByStatus: boolean;
    onlyActive: boolean;
  },
  filteredSoutenances: Soutenance[],
  groups: Group[]
) => {
  const workbook = new ExcelJS.Workbook();
  workbook.creator = "Systme de Gestion des Soutenances";
  workbook.lastModifiedBy = "Application PFE";

  const worksheet = workbook.addWorksheet("Planning des Soutenances", {
    properties: { tabColor: { argb: "4F46E5" } },
  });

  worksheet.columns = [
    { header: "Jury", key: "jury", width: 22 },
    { header: "Date", key: "date", width: 15 },
    { header: "Heure", key: "heure", width: 10 },
    { header: "N", key: "numero", width: 8 },
    { header: "Nom", key: "nom", width: 40 },
    { header: "Lieu", key: "lieu", width: 15 },
  ];

  const headerRow = worksheet.getRow(1);
  headerRow.height = 28;
  headerRow.eachCell((cell) => {
    cell.font = { bold: true, color: { argb: "FFFFFF" }, size: 12 };
    cell.fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "4F46E5" },
    };
    cell.alignment = { horizontal: "center", vertical: "center", wrapText: true };
    cell.border = { top: { style: "thin" }, left: { style: "thin" }, bottom: { style: "thin" }, right: { style: "thin" } };
  });

  let dataToExport = [...filteredSoutenances];
  if (options.onlyActive) {
    dataToExport = dataToExport.filter((s) => s.status === "Scheduled" || s.status === "Pending");
  }

  let rowIndex = 2;

  if (options.groupByJury) {
    const juryGroups: { [key: string]: Soutenance[] } = {};
    dataToExport.forEach((soutenance) => {
      const juryKey = soutenance.juryNames.join(", ");
      if (!juryGroups[juryKey]) juryGroups[juryKey] = [];
      juryGroups[juryKey].push(soutenance);
    });

    const sortedJuries = Object.keys(juryGroups).sort();
    sortedJuries.forEach((jury) => {
      const jurySoutenances = juryGroups[jury].sort((a, b) => {
        const dateA = new Date(a.date + "T" + a.time);
        const dateB = new Date(b.date + "T" + b.time);
        return dateA.getTime() - dateB.getTime();
      });

      const juryHeaderRow = worksheet.addRow([jury, "", "", "", "", ""]);
      juryHeaderRow.getCell(1).font = { bold: true };
      juryHeaderRow.getCell(1).fill = { type: "pattern", pattern: "solid", fgColor: { argb: "F3F4F6" } };
      worksheet.mergeCells(`A${rowIndex}:F${rowIndex}`);
      juryHeaderRow.height = 22;
      rowIndex++;

      jurySoutenances.forEach((soutenance) => {
        const groupNames = Array.isArray(soutenance.groupNames) ? soutenance.groupNames : [soutenance.groupNames || "N/A"];
        groupNames.forEach((groupId) => {
          const group = groups.find((g) => g.idGroupe === groupId) || { nomGroupe: groupId, students: ["N/A"] };
          const students = group.students.join(", ");
          const row = worksheet.addRow({
            jury: "",
            date: new Date(soutenance.date).toLocaleDateString("fr-FR"),
            heure: soutenance.time,
            numero: group.nomGroupe,
            nom: students,
            lieu: soutenance.location,
          });

          if (options.colorByStatus) {
            const statusCell = row.getCell(3);
            const bgColor = {
              Scheduled: "E6F0FF",
              Completed: "E6FFE6",
              Pending: "FFF9E6",
              Cancelled: "FFE6E6",
            }[soutenance.status] || "FFFFFF";
            statusCell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: bgColor } };
          }

          row.eachCell((cell) => {
            cell.border = { top: { style: "thin" }, left: { style: "thin" }, bottom: { style: "thin" }, right: { style: "thin" } };
            cell.alignment = { vertical: "middle", ...(cell.col === 5 ? { wrapText: true } : {}) };
          });
          rowIndex++;
        });
      });

      worksheet.addRow([]).height = 10;
      rowIndex++;
    });
  } else {
    const sortedSoutenances = dataToExport.sort((a, b) => {
      const dateA = new Date(a.date + "T" + a.time);
      const dateB = new Date(b.date + "T" + b.time);
      return dateA.getTime() - dateB.getTime();
    });

    sortedSoutenances.forEach((soutenance) => {
      const groupNames = Array.isArray(soutenance.groupNames) ? soutenance.groupNames : [soutenance.groupNames || "N/A"];
      groupNames.forEach((groupId) => {
        const group = groups.find((g) => g.idGroupe === groupId) || { nomGroupe: groupId, students: ["N/A"] };
        const students = group.students.join(", ");
        const row = worksheet.addRow({
          jury: soutenance.juryNames.join(", "),
          date: new Date(soutenance.date).toLocaleDateString("fr-FR"),
          heure: soutenance.time,
          numero: group.nomGroupe,
          nom: students,
          lieu: soutenance.location,
        });

        if (options.colorByStatus) {
          const statusCell = row.getCell(3);
          const bgColor = {
            Scheduled: "E6F0FF",
            Completed: "E6FFE6",
            Pending: "FFF9E6",
            Cancelled: "FFE6E6",
          }[soutenance.status] || "FFFFFF";
          statusCell.fill = { type: "pattern", pattern: "solid", fgColor: { argb: bgColor } };
        }

        row.eachCell((cell) => {
          cell.border = { top: { style: "thin" }, left: { style: "thin" }, bottom: { style: "thin" }, right: { style: "thin" } };
          cell.alignment = { vertical: "middle", ...(cell.col === 5 ? { wrapText: true } : {}) };
        });
        rowIndex++;
      });
    });
  }

  if (options.includeTitle) {
    worksheet.spliceRows(1, 0, [], []);
    worksheet.mergeCells("A1:F1");
    const titleCell = worksheet.getCell("A1");
    titleCell.value = "PLANNING DES SOUTENANCES PFE";
    titleCell.font = { name: "Arial", size: 16, bold: true, color: { argb: "4F46E5" } };
    titleCell.alignment = { vertical: "middle", horizontal: "center" };
    worksheet.getRow(1).height = 30;

    const academicYear = `${new Date().getFullYear() - 1}-${new Date().getFullYear()}`;
    worksheet.mergeCells("A2:F2");
    const subtitleCell = worksheet.getCell("A2");
    subtitleCell.value = `Anne universitaire ${academicYear}`;
    subtitleCell.font = { size: 12, italic: true, color: { argb: "666666" } };
    subtitleCell.alignment = { vertical: "middle", horizontal: "center" };
    worksheet.getRow(2).height = 20;

    worksheet.views = [{ state: "frozen", xSplit: 0, ySplit: options.includeHeaders ? 3 : 2 }];
  } else {
    worksheet.views = [{ state: "frozen", xSplit: 0, ySplit: options.includeHeaders ? 1 : 0 }];
  }

  const buffer = await workbook.xlsx.writeBuffer();
  const currentDate = new Date().toISOString().split("T")[0];
  saveAs(new Blob([buffer]), `Planning_Soutenances_PFE_${currentDate}.xlsx`);
};

const handleExportPlanningCSV = (
  options: {
    groupByJury?: boolean;
    includeHeaders: boolean;
    includeTitle: boolean;
    exportFormat?: string;
    colorByStatus?: boolean;
    onlyActive: boolean;
  },
  filteredSoutenances: Soutenance[],
  groups: Group[]
) => {
  let dataToExport = [...filteredSoutenances];
  if (options.onlyActive) {
    dataToExport = dataToExport.filter((s) => s.status === "Scheduled" || s.status === "Pending");
  }

  const headers = ["Jury", "Date", "Heure", "N", "Nom", "Lieu"];
  const rows = dataToExport.flatMap((soutenance) => {
    const groupNames = Array.isArray(soutenance.groupNames) ? soutenance.groupNames : [soutenance.groupNames || "N/A"];
    return groupNames.map((groupId) => {
      const group = groups.find((g) => g.idGroupe === groupId) || { nomGroupe: groupId, students: ["N/A"] };
      const students = group.students.join(", ");
      const jury = soutenance.juryNames.join(", ");
      return [
        `"${jury}"`,
        `"${new Date(soutenance.date).toLocaleDateString("fr-FR")}"`,
        `"${soutenance.time}"`,
        `"${group.nomGroupe}"`,
        `"${students}"`,
        `"${soutenance.location}"`,
      ];
    });
  });

  let csvContent = "";
  if (options.includeTitle) {
    const academicYear = `${new Date().getFullYear() - 1}-${new Date().getFullYear()}`;
    csvContent += `"PLANNING DES SOUTENANCES PFE"\n"Anne universitaire ${academicYear}"\n\n`;
  }
  if (options.includeHeaders) csvContent += headers.join(",") + "\n";
  csvContent += rows.map((row) => row.join(",")).join("\n");

  const blob = new Blob([csvContent], { type: "text/csv;charset=utf-8;" });
  const currentDate = new Date().toISOString().split("T")[0];
  saveAs(blob, `Planning_Soutenances_PFE_${currentDate}.csv`);
};

export default function SoutenancesPage() {
  const [soutenances, setSoutenances] = useState<Soutenance[]>([]);
  const [filteredSoutenances, setFilteredSoutenances] = useState<Soutenance[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [groups, setGroups] = useState<Group[]>([]);
  const [salles, setSalles] = useState<Salle[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [statusFilter, setStatusFilter] = useState<string>("all");
  const [activeTab, setActiveTab] = useState("all");
  const currentYear = new Date().getFullYear().toString();
  const [yearFilter, setYearFilter] = useState(currentYear);
  const [locationFilter, setLocationFilter] = useState("all");
  const [juryFilter, setJuryFilter] = useState("all");
  const router = useRouter();

  const rooms: Salle[] = [
    { idSalle: "1", nomSalle: "Salle 1" },
    { idSalle: "2", nomSalle: "Salle 2" },
    { idSalle: "3", nomSalle: "Salle 3" },
    { idSalle: "4", nomSalle: "Salle 4" },
    { idSalle: "5", nomSalle: "Salle 5" },
    { idSalle: "6", nomSalle: "Salle 6" },
  ];

  const juryMembers = [
    "Prof. Martin",
    "Dr. Dubois",
    "Prof. Laurent",
    "Dr. Bernard",
    "Prof. Moreau",
    "Dr. Petit",
  ];

  const fetchSoutenances = useCallback(async () => {
    try {
      const response = await fetch("http://localhost:5000/api/soutenance");
      if (!response.ok) throw new Error("Failed to fetch soutenances");
      const data = await response.json();
      // Normalize groupNames to always be an array
      const normalizedData = data.map((s: Soutenance) => ({
        ...s,
        groupNames: Array.isArray(s.groupNames) ? s.groupNames : s.groupNames ? [s.groupNames] : [],
        juryNames: Array.isArray(s.juryNames) ? s.juryNames : s.juryNames ? [s.juryNames] : [],
      }));
      setSoutenances(normalizedData);
    } catch (err) {
      setError("Erreur lors du chargement des soutenances");
    }
  }, []);

  const fetchGroupsAndSalles = useCallback(async () => {
    try {
      const groupResponse = await fetch("http://localhost:5000/api/groups");
      if (!groupResponse.ok) throw new Error("Failed to fetch groups");
      const groupData = await groupResponse.json();
      setGroups(groupData);
      setSalles(rooms);
    } catch (err) {
      setError("Erreur lors du chargement des groupes ou des salles");
    }
  }, []);

  useEffect(() => {
    const loadData = async () => {
      setIsLoading(true);
      await Promise.all([fetchSoutenances(), fetchGroupsAndSalles()]);
      setIsLoading(false);
    };
    loadData();
  }, [fetchSoutenances, fetchGroupsAndSalles]);

  const applyFilters = useCallback(() => {
    let filtered = [...soutenances];
    if (searchTerm) {
      const lowerSearchTerm = searchTerm.toLowerCase();
      filtered = filtered.filter((s) => {
        const groupNames = Array.isArray(s.groupNames) ? s.groupNames : [s.groupNames || "N/A"];
        return (
          groupNames.some((groupId) => {
            const group = groups.find((g) => g.idGroupe === groupId);
            return (
              group &&
              (group.nomGroupe.toLowerCase().includes(lowerSearchTerm) ||
                group.students.some((student) =>
                  student.toLowerCase().includes(lowerSearchTerm)
                ))
            );
          }) ||
          s.juryNames.some((jury) => jury.toLowerCase().includes(lowerSearchTerm)) ||
          s.location.toLowerCase().includes(lowerSearchTerm)
        );
      });
    }
    if (statusFilter !== "all") filtered = filtered.filter((s) => s.status === statusFilter);
    if (yearFilter !== "all") filtered = filtered.filter((s) => new Date(s.date).getFullYear().toString() === yearFilter);
    if (locationFilter !== "all") filtered = filtered.filter((s) => s.location === locationFilter);
    if (juryFilter !== "all") filtered = filtered.filter((s) => s.juryNames.some((jury) => jury === juryFilter));
    setFilteredSoutenances(filtered);
  }, [soutenances, searchTerm, statusFilter, yearFilter, locationFilter, juryFilter, groups]);

  useEffect(() => {
    applyFilters();
  }, [applyFilters]);

  const handleDelete = async (id: number) => {
    try {
      const response = await fetch(`http://localhost:5000/api/soutenance/${id}`, { method: "DELETE" });
      if (!response.ok) throw new Error("Failed to delete soutenance");
      setSoutenances((prev) => prev.filter((s) => s.idSoutenance !== id));
      setFilteredSoutenances((prev) => prev.filter((s) => s.idSoutenance !== id));
    } catch (err) {
      setError("Erreur lors de la suppression de la soutenance");
    }
  };

  const handleExportExcel = () => {
    const wb = utils.book_new();
    const excelData = filteredSoutenances.flatMap((soutenance) => {
      const groupNames = Array.isArray(soutenance.groupNames) ? soutenance.groupNames : [soutenance.groupNames || "N/A"];
      return groupNames.map((groupId) => {
        const group = groups.find((g) => g.idGroupe === groupId) || { nomGroupe: groupId, students: ["N/A"] };
        const students = group.students.join(", ");
        const jury = soutenance.juryNames.join(", ");
        const day = new Date(soutenance.date).toLocaleDateString("fr-FR");
        const time = soutenance.time;
        const statusConfig = getStatusConfig(soutenance.status);
        return { Date: day, Heure: time, Salle: soutenance.location, Groupe: group.nomGroupe, Etudiants: students, Jury: jury, Statut: statusConfig.text };
      });
    });

    const ws = utils.json_to_sheet(excelData);
    ws["!cols"] = [{ wch: 15 }, { wch: 10 }, { wch: 15 }, { wch: 12 }, { wch: 40 }, { wch: 30 }, { wch: 15 }];
    const range = { s: { r: 0, c: 0 }, e: { r: 0, c: 6 } };
    for (let col = range.s.c; col <= range.e.c; col++) {
      const cellAddress = utils.encode_cell({ r: 0, c: col });
      const cell = ws[cellAddress];
      if (cell) cell.s = { font: { bold: true, color: { rgb: "FFFFFF" } }, fill: { fgColor: { rgb: "4F46E5" } }, alignment: { horizontal: "center", vertical: "center" } };
    }

    utils.book_append_sheet(wb, ws, "Soutenances PFE");
    const currentDate = new Date().toISOString().split("T")[0];
    writeFile(wb, `Planning_Soutenances_PFE_${currentDate}.xlsx`);
  };

  const ExportPlanningDialog = () => {
    const [open, setOpen] = useState(false);
    const [exportOptions, setExportOptions] = useState({
      groupByJury: true,
      includeHeaders: true,
      includeTitle: true,
      exportFormat: "xlsx",
      colorByStatus: true,
      onlyActive: false,
    });

    const handleOptionChange = (option: string, value: string | boolean) => {
      setExportOptions((prev) => ({ ...prev, [option]: value }));
    };

    const handleExport = async () => {
      if (exportOptions.exportFormat === "xlsx") {
        await handleExportPlanningExcel(exportOptions, filteredSoutenances, groups);
      } else {
        handleExportPlanningCSV(exportOptions, filteredSoutenances, groups);
      }
      setOpen(false);
    };

    return (
      <Dialog open={open} onOpenChange={setOpen}>
        <DialogTrigger asChild>
          <Button className="bg-secondary hover:bg-secondary/90 text-secondary-foreground rounded-full px-4 py-2 flex items-center gap-2">
            <Download className="h-5 w-5" />
            Exporter Planning
          </Button>
        </DialogTrigger>
        <DialogContent className="sm:max-w-md">
          <DialogHeader>
            <DialogTitle>Exporter le planning des soutenances</DialogTitle>
            <DialogDescription>Personnalisez les options d'exportation du planning des soutenances PFE.</DialogDescription>
          </DialogHeader>
          <div className="grid gap-4 py-4">
            <div className="flex flex-col gap-2">
              <Label className="font-medium">Format d'exportation</Label>
              <RadioGroup defaultValue={exportOptions.exportFormat} onValueChange={(value) => handleOptionChange("exportFormat", value)} className="flex gap-4">
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="xlsx" id="xlsx" />
                  <Label htmlFor="xlsx">Excel (.xlsx)</Label>
                </div>
                <div className="flex items-center space-x-2">
                  <RadioGroupItem value="csv" id="csv" />
                  <Label htmlFor="csv">CSV (.csv)</Label>
                </div>
              </RadioGroup>
            </div>
            <div className="grid gap-2">
              <Label className="font-medium">Options d'affichage</Label>
              <div className="flex flex-col gap-3">
                <div className="flex items-center space-x-2">
                  <Checkbox id="groupByJury" checked={exportOptions.groupByJury} onCheckedChange={(checked) => handleOptionChange("groupByJury", checked === true)} />
                  <Label htmlFor="groupByJury">Grouper par jury</Label>
                </div>
                <div className="flex items-center space-x-2">
                  <Checkbox id="includeTitle" checked={exportOptions.includeTitle} onCheckedChange={(checked) => handleOptionChange("includeTitle", checked === true)} />
                  <Label htmlFor="includeTitle">Inclure le titre et sous-titre</Label>
                </div>
                {exportOptions.exportFormat === "xlsx" && (
                  <div className="flex items-center space-x-2">
                    <Checkbox id="colorByStatus" checked={exportOptions.colorByStatus} onCheckedChange={(checked) => handleOptionChange("colorByStatus", checked === true)} />
                    <Label htmlFor="colorByStatus">Colorer selon le statut</Label>
                  </div>
                )}
                <div className="flex items-center space-x-2">
                  <Checkbox id="onlyActive" checked={exportOptions.onlyActive} onCheckedChange={(checked) => handleOptionChange("onlyActive", checked === true)} />
                  <Label htmlFor="onlyActive">Seulement les soutenances actives</Label>
                </div>
              </div>
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={() => setOpen(false)}>Annuler</Button>
            <Button onClick={handleExport} className="bg-primary text-white">Exporter</Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    );
  };

  if (isLoading) {
    return (
      <div className="container mx-auto p-6">
        <Skeleton className="h-12 w-1/3 mb-6" />
        <Skeleton className="h-10 w-full mb-4" />
        <div className="space-y-4">
          {Array.from({ length: 5 }).map((_, index) => (
            <Skeleton key={index} className="h-16 w-full" />
          ))}
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="container mx-auto p-6">
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      </div>
    );
  }

  return (
    <div className="container mx-auto p-6">
      <Card>
        <CardHeader className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4">
          <div className="flex items-center gap-4">
            <Tabs value={activeTab} onValueChange={setActiveTab}>
              <TabsList>
                <TabsTrigger value="all" onClick={() => setStatusFilter("all")}>Tous</TabsTrigger>
                <TabsTrigger value="Scheduled" onClick={() => setStatusFilter("Scheduled")}>Planifi</TabsTrigger>
                <TabsTrigger value="Completed" onClick={() => setStatusFilter("Completed")}>Termin</TabsTrigger>
                <TabsTrigger value="Pending" onClick={() => setStatusFilter("Pending")}>En attente</TabsTrigger>
                <TabsTrigger value="Cancelled" onClick={() => setStatusFilter("Cancelled")}>Annul</TabsTrigger>
              </TabsList>
            </Tabs>
          </div>
          <div className="flex flex-col sm:flex-row gap-4 w-full sm:w-auto">
            <div className="relative flex-1">
              <Search className="absolute left-2.5 top-1/2 transform -translate-y-1/2 h-4 w-4 text-muted-foreground" />
              <Input
                className="pl-8 w-full"
                placeholder="Rechercher par groupe, jury ou lieu..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
            <Select value={yearFilter} onValueChange={setYearFilter}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filtrer par anne" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Toutes les annes</SelectItem>
                <SelectItem value="2023">2023</SelectItem>
                <SelectItem value="2024">2024</SelectItem>
                <SelectItem value="2025">2025</SelectItem>
              </SelectContent>
            </Select>
            <Select value={locationFilter} onValueChange={setLocationFilter}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filtrer par lieu" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Tous les lieux</SelectItem>
                {salles.map((salle) => (
                  <SelectItem key={salle.idSalle} value={salle.nomSalle}>{salle.nomSalle}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <Select value={juryFilter} onValueChange={setJuryFilter}>
              <SelectTrigger className="w-full sm:w-[180px]">
                <SelectValue placeholder="Filtrer par jury" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="all">Tous les jurys</SelectItem>
                {juryMembers.map((jury) => (
                  <SelectItem key={jury} value={jury}>{jury}</SelectItem>
                ))}
              </SelectContent>
            </Select>
            <ExportPlanningDialog />
            <Button
              onClick={() => router.push("/soutenances/add")}
              className="bg-primary hover:bg-primary/90 text-white rounded-full px-4 py-2 flex items-center gap-2"
            >
              <Plus className="h-5 w-5" />
              Ajouter
            </Button>
          </div>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Date</TableHead>
                <TableHead>Heure</TableHead>
                <TableHead>Lieu</TableHead>
                <TableHead>Groupes</TableHead>
                <TableHead>Jury</TableHead>
                <TableHead>Statut</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredSoutenances.map((soutenance) => {
                const groupNames = Array.isArray(soutenance.groupNames) ? soutenance.groupNames : [soutenance.groupNames || "N/A"];
                return (
                  <TableRow key={soutenance.idSoutenance}>
                    <TableCell>{new Date(soutenance.date).toLocaleDateString("fr-FR")}</TableCell>
                    <TableCell>{soutenance.time}</TableCell>
                    <TableCell>{soutenance.location}</TableCell>
                    <TableCell>
                      {groupNames.map((groupId) => {
                        const group = groups.find((g) => g.idGroupe === groupId) || { nomGroupe: groupId, students: ["N/A"] };
                        return (
                          <div key={groupId} className="mb-1">
                            <span className="font-medium">{group.nomGroupe}</span>
                            <br />
                            <span className="text-sm text-muted-foreground">{group.students.join(", ")}</span>
                          </div>
                        );
                      })}
                    </TableCell>
                    <TableCell>
                      <TooltipProvider>
                        <Tooltip>
                          <TooltipTrigger>{soutenance.juryNames.join(", ")}</TooltipTrigger>
                          <TooltipContent>
                            <p>{soutenance.juryNames.join(", ")}</p>
                          </TooltipContent>
                        </Tooltip>
                      </TooltipProvider>
                    </TableCell>
                    <TableCell>
                      <StatusBadge status={soutenance.status} />
                    </TableCell>
                    <TableCell className="text-right space-x-2">
                      {new Date(soutenance.date).getFullYear() === new Date().getFullYear() && (
                        <>
                          <Button variant="ghost" size="icon" onClick={() => router.push(`/soutenances/edit/${soutenance.idSoutenance}`)}>
                            <Edit2 className="h-4 w-4" />
                            <span className="sr-only">Modifier la soutenance</span>
                          </Button>
                          <DeleteDialog onDelete={() => handleDelete(soutenance.idSoutenance)} idSoutenance={soutenance.idSoutenance} />
                        </>
                      )}
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
          {filteredSoutenances.length === 0 && (
            <div className="text-center py-10 text-muted-foreground">Aucune soutenance trouve avec les filtres actuels.</div>
          )}
        </CardContent>
      </Card>
    </div>
  );
}